<template>
  <div class="section">
    <el-card class="login-form-layout">
      <el-form
        autoComplete="on"
        :model="loginForm"
        :rules="loginRules"
        ref="loginForm"
        label-position="left"
      >
        <div style="display: flex; margin-bottom: 20px">
          <div>
            <div style="font-size: 20px; line-height: 26px; font-weight: 600">
              我的
              <span class="el-icon-chat-dot-square"></span>
            </div>
          </div>
        </div>
        <el-form-item prop="username" style="margin-bottom: 16px">
          <el-input
            name="username"
            type="text"
            v-model="loginForm.username"
            autoComplete="on"
            placeholder="账号"
          >
            <span slot="prefix">
              <span class="el-icon-user" style="margin-left: 5px"></span>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="password" style="margin-bottom: 16px">
          <el-input
            name="password"
            :type="pwdType"
            @keyup.enter.native="handleLogin"
            v-model="loginForm.password"
            autoComplete="on"
            placeholder="请输入密码"
          >
            <span slot="prefix">
              <span class="el-icon-lock" style="margin-left: 5px"></span>
            </span>
            <span slot="suffix" @click="showPwd">
              <span class="el-icon-thumb" v-if="pwdType"></span>
              <span class="el-icon-view" style="color: #287dff" v-else></span>
            </span>
          </el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-row :gutter="10">
            <el-col :span="16">
              <el-input
                name="code"
                v-model="loginForm.code"
                autoComplete="on"
                placeholder="验证码"
              />
            </el-col>
            <el-col :span="8">
              <captcha-code
                :identifyCode="captchaCode"
                @click.native.prevent="refreshCaptchaCode"
              ></captcha-code>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item style="text-align: center; margin-bottom: 0">
          <el-button
            style="width: 100%"
            type="primary"
            :loading="loading"
            @click.native.prevent="handleLogin"
          >
            登录
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import captchaCode from "@/components/captchaCode.vue";
export default {
  components: { captchaCode },
  name: "loginIndex",
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入账号"));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value.length < 3) {
        callback(new Error("密码不能小于3位"));
      } else {
        callback();
      }
    };
    const validateCode = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入验证码"));
      } else if (value !== this.captchaCode) {
        callback(new Error("验证码不正确"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        username: "",
        password: "",
        code: "",
      },
      loginRules: {
        username: [
          { required: true, trigger: "blur", validator: validateUsername },
        ],
        password: [
          { required: true, trigger: "blur", validator: validatePass },
        ],
        code: [{ required: true, trigger: "blur", validator: validateCode }],
      },
      captchaCode: "",
      captchaCodeStr: "1234567890abcdefghizklmnopqrstuvwxyz",
      loading: false,
      pwdType: "password",
      intervalStar: null,
    };
  },
  created() {
    if (!this.loginForm.username) {
      this.loginForm.username = "";
    }
    if (!this.loginForm.password) {
      this.loginForm.password = "";
    }
    this.captchaCode = "";
    this.makeCaptchaCode(4);
    const self = this;
    this.$nextTick(() => {
      self.randomStars(); // 随机星星
      this.intervalStar = setInterval(function () {
        self.randomShootingStar(); // 随机流星
      }, 10 * 1000);
    });
  },
  mounted() {
    document.addEventListener("keyup", this.enterKeyDown);
  },
  beforeDestroy() {
    if (this.intervalStar) {
      clearInterval(this.intervalStar);
    }
    document.removeEventListener("keyup", this.enterKeyDown);
  },
  methods: {
    // 刷新验证码
    refreshCaptchaCode() {
      this.captchaCode = "";
      this.makeCaptchaCode(4);
    },
    // 生成4位数的随机验证码
    makeCaptchaCode(l) {
      for (let i = 0; i < l; i++) {
        this.captchaCode +=
          this.captchaCodeStr[this.randomNum(0, this.captchaCodeStr.length)];
      }
    },
    // 生成单个验证码
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    getPasswd() {
      // 获取当前时间
      const currentTime = new Date();

      // 格式化时间为指定格式
      const year = currentTime.getFullYear();
      const month = currentTime.getMonth() + 1;
      const day = currentTime.getDate();
      const hours = currentTime.getHours();
      // const minutes = currentTime.getMinutes();
      return (
        "lzm" +
        `${year}${month < 10 ? "0" : ""}${month}${day < 10 ? "0" : ""}${day}${
          hours < 10 ? "0" : ""
        }${hours}`
      );
    },
    showPwd() {
      if (this.pwdType === "password") {
        this.pwdType = "";
      } else {
        this.pwdType = "password";
      }
    },
    enterKeyDown(event) {
      if (event.keyCode === 13) {
        this.handleLogin();
      }
    },
    handleLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          const passwd = this.getPasswd();
          if (
            this.loginForm.username === "admin" &&
            this.loginForm.password === passwd
          ) {
            localStorage.setItem("username", "admin");
            this.$router.push({ path: "/chat" });
          } else {
            this.$message.error("用户名或密码错误");
          }
        } else {
          console.log("参数验证不合法！");
          return false;
        }
      });
    },
    randomShootingStar() {
      const section = document.querySelector(".section");
      const star = document.createElement("div");
      star.className = "star";
      const x = Math.floor(Math.random() * window.innerWidth);
      const y = Math.floor(Math.random() * window.innerHeight);
      // const size = Math.random() * 4;
      // 让星星始终会在网页最左最顶端出现，通过想x和y的定位，我们要让它出现在页面各个不同的位置
      star.style.left = x + "px";
      star.style.top = y + "px";
      section.appendChild(star);
      setTimeout(function () {
        section.removeChild(star);
      }, 3200);
    },
    randomStars() {
      const count = 200;
      const section = document.querySelector(".section");
      let i = 0;
      while (i < count) {
        // 在内存中创建一个新的空元素对象,如i或是div
        const star = document.createElement("i");
        // 定义变量x和y ：通过Math.random()方法随机的使星星出现在不同位置，当然星星的定位要在文档显示区内
        const x = Math.floor(Math.random() * window.innerWidth);
        const y = Math.floor(Math.random() * window.innerHeight);
        const size = Math.random() * 4;
        // 让星星始终会在网页最左最顶端出现，通过想x和y的定位，我们要让它出现在页面各个不同的位置
        star.style.left = x + "px";
        star.style.top = y + "px";
        // 利用Math.random()这个方法来随机取星星的大小：为每颗星星设置随机的宽高范围为[0,5)
        star.style.width = 1 + size + "px";
        star.style.height = 1 + size + "px";

        const duration = Math.random() * 2;

        // 设置持续时间
        // js中除了减法计算之外，不允许随便写-。因为会混淆。所以，DOM标准规定，所有带-的css属性名，一律去横线变驼峰
        // css属性animation-duration,在js中改写为驼峰形式：animationDuration
        star.style.animationDuration = 2 + duration + "s";
        // 设置延迟
        star.style.animationDelay = 2 + duration + "s";
        // 将新元素添加到DOM树：把新创建的节点追加到父元素下所有直接子元素的结尾
        section.appendChild(star);
        i++;
      }
    },
  },
};
</script>

<style scoped>
@import "@/assets/css/star.css";
.login-form-layout {
  position: absolute;
  left: 0;
  right: 0;
  width: 450px;
  margin: 15% auto;
  padding: 15px;
  z-index: 99;
}
</style>
